.D {
  color: #66bdf4;
}

.R {
  color: #f87773;
}

.I {
  color: #a4a2a4;
}

.navbar {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;
  padding: .5rem 1rem;
  padding: 0.5em 1em;
  line-height: 1;
  border-bottom: #ff4741 2px solid;
  margin-bottom: 0;
  background-color: #ffffff;
  /* Small desktop */
  /* Large desktop */
  /* Extra Large desktop */
}

.navbar.justify-content-between {
  -webkit-box-pack: justify !important;
  justify-content: space-between !important;
}

.navbar ul {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}

.navbar ul li {
  font-family: 'Raleway';
  display: block;
  position: relative;
  float: left;
  padding-left: 10px;
}

.navbar .navbar-brand {
  padding: 0;
}

.navbar .navbar-brand img {
  width: 125px;
}

.navbar a.linkOut {
  color: #132149;
}

.navbar a.linkOut.social-icons {
  padding-right: 0;
  padding-left: 5px;
  padding-top: 15px;
  font-size: 1.6em;
}

.navbar a.linkOut:hover,
.navbar a.linkOut:active,
.navbar a.linkOut:visited {
  text-decoration: none;
  color: #132149;
}

.navbar a.linkOut .main-site {
  background-image: url("../../assets/images/THP_logo_circle.svg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 20px;
  height: 20px;
  display: inline-block;
}

.navbar .navbar-right {
  float: right;
}

@media (min-width: 992px) {
  .navbar {
    padding: 0.5em 1em;
    line-height: 1;
  }
}

.navbar .navbar-brand {
  font-size: 2em;
  line-height: 1.45;
}

@media (min-width: 1600px) {
  .navbar {
    padding: 0.25em 1em 0.15em 1em;
  }
}

.navbar .navbar-brand {
  font-size: 1.85em;
  line-height: 1.55;
}

.navbar a.linkOut {
  font-size: 1.3em;
}

@media (min-width: 1700px) {
  .navbar {
    padding: 0.2em 1em 0.1em 1em;
  }
}

.navbar .navbar-brand {
  font-size: 1.5em;
  line-height: 1.35;
}

.navbar a.linkOut {
  font-size: 1.15em;
}




